// æ ¼å¼ï¼šæ¡ä»¶ä¸­çš„å­—æ®µå -> [æ¥æºç±»å‹, å®é™…å­—æ®µå/è·¯å¾„]
// æ¥æºç±»å‹ï¼š'milestone' = ä»é‡Œç¨‹ç¢‘è¯»å–, 'direct' = ä» statData ç›´æ¥è¯»å–
