    'è¶Šçº§å‡»æ€æ•°': ['milestone', 'è¶Šçº§å‡»æ€æ•°'],
    'æœ€é«˜è¶Šçº§å±‚æ•°': ['milestone', 'æœ€é«˜è¶Šçº§å±‚æ•°'],
    'æ¿’æ­»é€ƒç”Ÿæ¬¡æ•°': ['milestone', 'æ¿’æ­»é€ƒç”Ÿæ¬¡æ•°'],
    'è¿èƒœæœ€é«˜çºªå½•': ['milestone', 'è¿èƒœæœ€é«˜çºªå½•'],
    'æ¢ç´¢åŒºåŸŸæ•°': ['milestone', 'æ¢ç´¢åŒºåŸŸæ•°'],
    'ç§˜å¢ƒé€šå…³æ•°': ['milestone', 'ç§˜å¢ƒé€šå…³æ•°'],
    'ç¦åœ°è¸è¶³æ•°': ['milestone', 'ç¦åœ°è¸è¶³æ•°'],
