    'å¤©å‘½æ‰€å½’æ¬¡æ•°': ['milestone', 'å¤©å‘½æ‰€å½’æ¬¡æ•°'],
    'å¥‡ç¼˜é­é‡æ¬¡æ•°': ['milestone', 'å¥‡ç¼˜é­é‡æ¬¡æ•°'],
