    // çº¢é¢œå¥½æ„Ÿåº¦æ€»å’Œ
    const çº¢é¢œå¥½æ„Ÿåº¦æ€»å’Œ = Object.values(çº¢é¢œ).reduce((sum, data) => sum + (data?.å¥½æ„Ÿåº¦ ?? 0), 0);
