    const ç¥é€šåˆ—è¡¨ = _.get(statData, 'æœ¬å°Š.ç¥é€šåˆ—è¡¨', {}) as Record<string, unknown>;
    const ç¥é€šé¢†æ‚Ÿæ•° = Object.keys(ç¥é€šåˆ—è¡¨).length;
