    // æ•Œå¯¹åŠ¿åŠ›æ•°ï¼šå£°æœ› <= -30 çš„åŠ¿åŠ›
    const æ•Œå¯¹åŠ¿åŠ›æ•° = Object.values(å£°æœ›ç³»ç»Ÿ).filter(data => (data?.å€¼ ?? 0) <= -30).length;
